import { createRouter,createWebHashHistory } from "vue-router";
import HomeManager from "@/pages/Home.vue";
import BannerManager from "@/pages/Banner.vue";
import PostManager from "@/pages/Post.vue";
import CommentManager from "@/pages/Comment.vue";
import UserManager from "@/pages/User.vue";
const routes = [{
    path: "/", component: HomeManager, name:"home"
},{
    path: "/banner", component: BannerManager, name:"banner"
},{
    path: "/post", component: PostManager, name:"post"
},{
    path: "/comment", component: CommentManager, name:"comment"
},{
    path: "/user", component: UserManager, name:"user"
}];

const router = createRouter({
    // 4. 内部提供了 history 模式的实现。为了简单起见，我们在这里使用 hash 模式。
    history: createWebHashHistory(),
    routes, // `routes: routes` 的缩写
  });

export default router;